.chess-container{
height:100%;
width:100%;
z-index: 9999;
position: fixed;
top:0;
left:0;
display: none;
justify-content: center;
align-items: center;
text-align: center;
}

.chess-like-frame-container{
	position: relative;
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: space-between; 
}
.chess-like-frame{
	pointer-events: none;
top:0;
left:0px;
	position: fixed;
	z-index: 999;
	flex: 1;
	width: 100%;
	height: 100%;
	object-fit: cover;
	display: block;
}


.hesitate{
	/* display:none; */
	  -webkit-animation: hesitate 2.5s forwards;
			  animation: hesitate 2.5s forwards;
	}
	


@-webkit-keyframes hesitate {
	0% {
	  opacity: 0
	}
	50% {
	  opacity: 0
	}
	
	100% {
	  opacity: 1
	}
  }

  .molly{
	left:3%;
  }